<template>
  <div class="bilibili-partion">
    <div class="container" v-if="result">
      <h2>{{ result.title }}</h2>
      <div class="user-videos">
        <a :href="video.link" target="_blank" rel="noopener noreferrer" class="user-video"
           v-for="video of result.items" :key="video.guid">
          <div class="video-cover"
               v-html="video.content.match(/<img .+?>/)[0]"></div>
          <div class="video-info">{{video.title}}</div>
        </a>
      </div>
    </div>
    <cell-group title="" v-if="!$route.params.tid">
      <mt-field label="" placeholder="" v-model="partionName"
                @focus.native.prevent
                @click.native.prevent.stop="popupVisible = true" disable-clear></mt-field>
    </cell-group>
    <button-group>
      <mt-button type="default" size="large" @click="fetchRSS" :disabled="!tid"></mt-button>
      <mt-button type="primary" size="large" v-if="!isFavorite" :disabled="!tid" @click="addToFavorites">
      </mt-button>
      <mt-button type="danger" size="large" v-else :disabled="!tid" @click="removeFromFavorites"></mt-button>
    </button-group>
    <mt-popup
      v-model="popupVisible"
      position="bottom"
      style="width:100%"
    >
      <mt-picker :slots="slots" @change="onValuesChange" value-key="name"></mt-picker>
    </mt-popup>
  </div>
</template>

<script>
  import axios from 'axios'
  import { Indicator, Toast } from 'mint-ui'
  import CellGroup from '@/components/weui/cell-group'
  import ButtonGroup from '@/components/button-group'
  import parse from '@/utils/parseRss'

  const partions = [
    {
      name: '',
      children: [
        { name: 'MADAMV', id: 24 },
        { name: 'MMD3D', id: 25 },
        { name: '', id: 47 },
        { name: '', id: 27 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 33 },
        { name: '', id: 32 },
        { name: '', id: 51 },
        { name: '', id: 152 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 153 },
        { name: '', id: 168 },
        { name: '', id: 169 },
        { name: '', id: 170 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 28 },
        { name: '', id: 31 },
        { name: 'VOCALOIDUTAU', id: 30 },
        { name: '', id: 59 },
        { name: '', id: 29 },
        { name: 'OP/ED/OST', id: 54 },
        { name: '', id: 130 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 20 },
        { name: '', id: 154 },
        { name: '', id: 156 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 17 },
        { name: '', id: 171 },
        { name: '', id: 172 },
        { name: '', id: 65 },
        { name: '', id: 173 },
        { name: 'GMV', id: 121 },
        { name: '', id: 136 },
        { name: 'Mugen', id: 19 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 124 },
        { name: '', id: 122 },
        { name: '', id: 39 },
        { name: '', id: 96 },
        { name: '', id: 95 },
        { name: '', id: 98 },
        { name: '', id: 176 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 138 },
        { name: '', id: 21 },
        { name: '', id: 76 },
        { name: '', id: 75 },
        { name: '', id: 161 },
        { name: '', id: 162 },
        { name: 'ASMR', id: 175 },
        { name: '', id: 163 },
        { name: '', id: 174 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 22 },
        { name: 'MAD', id: 26 },
        { name: 'VOCALOID', id: 126 },
        { name: '', id: 127 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 157 },
        { name: '', id: 158 },
        { name: '', id: 164 },
        { name: '', id: 159 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 166 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 71 },
        { name: '', id: 137 },
        { name: 'Korea', id: 131 }
      ]
    },
    {
      name: '',
      children: [
        { name: '', id: 182 },
        { name: '', id: 183 },
        { name: '', id: 85 },
        { name: '', id: 184 },
        { name: '', id: 86 }
      ]
    }
  ]

  export default {
    name: 'Partion',
    components: { ButtonGroup, CellGroup },
    data() {
      return {
        form: {
          tid: this.$route.query.tid || '',
        },

        result: null,
        popupVisible: false,
        slots: [
          {
            flex: 1,
            values: partions,
            className: 'slot1',
            textAlign: 'center'
          }, {
            divider: true,
            content: '-',
            className: 'slot2'
          }, {
            flex: 1,
            values: partions[0].children,
            className: 'slot3',
            textAlign: 'center'
          }
        ]
      }
    },
    computed: {
      flattenedPartions() {
        return partions.reduce((acc, { name: parentName, children }) => (
          [
            ...acc,
            ...children.map(({ name, id }) => ({ fullname: `${parentName}-${name}`, id }))
          ]
        ), [])
      },
      partionName: {
        get() {
          const partion = this.flattenedPartions.find(({ id }) => id === this.tid)
          return partion && partion.fullname
        },
        set() {

        }
      },
      tid() {
        return this.$route.params.tid || this.form.tid
      },
      rssPath() {
        return `/bilibili/partion/${this.tid}`
      },
      isFavorite() {
        return !!this.$store.state.favorites.find(({ rss }) => rss === this.rssPath)
      },
    },
    methods: {
      onValuesChange(picker, values) {
        this.form.tid = values[1].id
        picker.setSlotValues(1, values[0].children)
      },
      async fetchRSS() {
        Indicator.open()
        return axios(this.rssPath)
          .catch(() => Indicator.close())
          .then(async ({ data }) => {
            this.result = await parse(data)
            Indicator.close()
          })
      },
      async addToFavorites() {
        let title
        if (!this.result) {
          await this.fetchRSS()
        }
        title = this.partionName
        this.$store.commit('addFavorite', {
          type: 'bilibili',
          desc: '',
          title,
          path: `/bilibili/partion/${this.tid}`,
          rss: this.rssPath,
          autoUpdate: true,
        })
        Toast({
          message: '~',
          duration: 1500,
        })
      },
      removeFromFavorites() {
        this.$store.commit('removeFavorite', this.rssPath)
        Toast({
          message: '',
          duration: 1500,
        })
      },
    },
    created() {
      if (this.$route.params.tid) {
        this.fetchRSS()
      }
    },
  }
</script>

<style lang="less">
  .bilibili-partion {
    .user-videos {
      margin-bottom: 15px;

      .user-video {
        display: flex;
        padding: 10px 0;
        border-bottom: 1px solid #eee;

        .video-cover {
          flex-shrink: 0;
          height: 60px;
          border-radius: 4px;
          margin-right: 10px;
          overflow: hidden;

          img {
            display: block;
            height: 100%;
          }
        }
        .video-info {
          flex-grow: 1;
          font-size: 14px;
        }
      }
    }
  }
</style>



